import React from "react";
import { Link } from "react-router-dom";
import "./Nav.css";

const Navbar = () => {
  return (
    <nav className="navbar">
      <Link to="/todo"></Link>
      <Link to="/grade"></Link>
      <Link to="/schedule"></Link>
      <Link to="/my"></Link>
    </nav>
  );
};

export default Navbar;
