import React, { useState, useEffect } from "react";
import Calendar from "react-calendar";
import moment from "moment";
import styled from "styled-components";
import "react-calendar/dist/Calendar.css";
import "./ToDoListForm.css";
import { getDoc, doc, setDoc, updateDoc } from "firebase/firestore";
import { onAuthStateChanged } from "firebase/auth";
import { auth, db } from "../firebase"; // Firebase  

const StyledCalendarWrapper = styled.div`
  padding: 10px;
  max-width: 300px;
  margin: 0 auto;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 12px;
  background-color: white;
`;

const StyledCalendar = styled(Calendar)`
  & .react-calendar__navigation {
    margin-bottom: 10px;
  }

  & .react-calendar__month-view__weekdays__weekday {
    font-size: 0.85em;
  }

  & .react-calendar__tile {
    border-radius: 50%;

    &:hover {
      background-color: #e6e6e6;
    }
  }

  & .react-calendar__tile--now {
    background: #beecff; /*     */
  }

  & .react-calendar__month-view__days__day--weekend {
    color: red; /*     */
  }

  & .react-calendar__tile--active {
    background-color: #e6e6e6;
    color: white;
  }
`;

const ToDoListWrapper = styled.div`
  padding: 10px;
  max-width: 300px;
  margin: 20px auto;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 12px;
  background-color: white;

  ul {
    max-height: 160px; /*    */
    overflow-y: auto; /*    */
    margin: 0; /*    */
    padding: 0 10px; /*    */
  }
`;

const ToDoItem = styled.li`
  list-style: none;
  padding: 5px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const DeleteButton = styled.button`
  width: 80px; /*    */
  height: 30px; /*    */
  background-color: white; /*     */
  color: black; /*     */
  border: 2px solid blue; /*    */
  border-radius: 5px; /*    */
  padding: 5px 10px; /*    */
  cursor: pointer; /*     */
  font-size: 14px; /*    */
  &:hover {
    background-color: #f0f0f0; /*      */
  }
`;

const ToDoInput = styled.input`
  width: calc(100% - 22px);
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 5px;
`;

const ToDoListForm = () => {
  const [date, setDate] = useState(new Date());
  const [toDoList, setToDoList] = useState({});
  const [newToDo, setNewToDo] = useState("");
  const [userId, setUserId] = useState(null);

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (user) => {
      if (user) {
        setUserId(user.uid);
        fetchData(user.uid, date);
      }
    });
    return () => unsubscribe();
  }, [date]);

  const fetchData = async (userId, date) => {
    const dateString = moment(date).format("YYYY-MM-DD");
    const docRef = doc(db, "users", userId, "todos", dateString);
    const docSnap = await getDoc(docRef);
    if (docSnap.exists()) {
      setToDoList({ [dateString]: docSnap.data().items });
    } else {
      setToDoList({});
    }
  };

  const handleDateChange = (newDate) => {
    setDate(newDate);
    if (userId) {
      fetchData(userId, newDate);
    }
  };

  const handleAddToDo = () => {
    if (newToDo) {
      const dateString = moment(date).format("YYYY-MM-DD");
      setToDoList((prevToDoList) => {
        const newDateList = prevToDoList[dateString]
          ? [...prevToDoList[dateString], newToDo]
          : [newToDo];
        saveToDoToFirestore(userId, dateString, newDateList); // Firestore 
        return { ...prevToDoList, [dateString]: newDateList };
      });
      setNewToDo("");
    }
  };

  const saveToDoToFirestore = async (userId, dateString, newDateList) => {
    const docRef = doc(db, "users", userId, "todos", dateString);
    const docSnap = await getDoc(docRef);

    if (docSnap.exists()) {
      await updateDoc(docRef, {
        items: newDateList,
      });
    } else {
      await setDoc(docRef, {
        items: newDateList,
      });
    }
  };

  const handleInputChange = (e) => {
    setNewToDo(e.target.value);
  };

  //    
  const handleDeleteToDo = (dateString, index) => {
    setToDoList((prevToDoList) => {
      const updatedToDoList = prevToDoList[dateString].filter((_, i) => i !== index);
      saveToDoToFirestore(userId, dateString, updatedToDoList); // Firestore 
      return { ...prevToDoList, [dateString]: updatedToDoList };
    });
  };

  const dateString = moment(date).format("YYYY-MM-DD");
  const toDosForSelectedDate = toDoList[dateString] || [];

  return (
    <div>
      <StyledCalendarWrapper>
        <StyledCalendar
          value={date}
          onChange={handleDateChange}
          formatDay={(locale, date) => moment(date).format("D")} //    
          formatYear={(locale, date) => moment(date).format("YYYY")} //     
          formatMonthYear={(locale, date) => moment(date).format("YYYY. MM")} //  2024. 12   
          calendarType="gregory" //   
          showNeighboringMonth={false} // ,   
          next2Label={null} // +1 & +10   
          prev2Label={null} // -1 & -10   
          minDetail="year" // 10  
        />
      </StyledCalendarWrapper>
      <ToDoListWrapper>
        <h3>{moment(date).format("YYYY-MM-DD")}  </h3>
        <ul>
          {toDosForSelectedDate.map((toDo, index) => (
            <ToDoItem key={index}>
              {toDo}
              <DeleteButton
                className="DeleteButton"
                onClick={() => handleDeleteToDo(dateString, index)}
              >
                
              </DeleteButton>
            </ToDoItem>
          ))}
        </ul>
        <ToDoInput
          type="text"
          value={newToDo}
          onChange={handleInputChange}
          placeholder="   "
        />
        <button className="todo-btn" onClick={handleAddToDo}>
            
        </button>
      </ToDoListWrapper>
    </div>
  );
};

export default ToDoListForm;
