import React, { useState, useEffect } from "react";
import "./MyForm.css";
import { useNavigate } from "react-router-dom";
import { getDoc, doc, updateDoc } from "firebase/firestore";
import { db, auth } from "../firebase"; // Firebase Firestore  Authentication  

const MyForm = () => {
  const [username, setUsername] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");

  const navigate = useNavigate();

  useEffect(() => {
    //      
    const fetchUserData = async () => {
      try {
        const currentUser = auth.currentUser;
        if (currentUser) {
          const userId = currentUser.uid; //   UID
          const userDocRef = doc(db, "users", userId); //   
          const userDocSnap = await getDoc(userDocRef); //   
          if (userDocSnap.exists()) {
            const userData = userDocSnap.data(); //  
            console.log(" :", userData); //    
            setUsername(userData.username || "");
            setEmail(userData.email || "");
            setPhone(userData.phone || "");
          } else {
            console.error("   .");
          }
        }
      } catch (error) {
        console.error("   :", error);
      }
    };

    //   
    fetchUserData();
  }, []);

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const currentUser = auth.currentUser;
      if (currentUser) {
        const userId = currentUser.uid; //   UID
        const userDocRef = doc(db, "users", userId); //   
        await updateDoc(userDocRef, {
          username: username,
          email: email,
          phone: phone,
        });
        alert("   .");
      }
    } catch (error) {
      console.error("   :", error);
      alert("   .");
    }
  };

  const handleLogoutButtonClick = async () => {
    try {
      await auth.signOut(); // Firebase 
      alert(" .");
      navigate("/"); //      
    } catch (error) {
      console.error(" :", error);
      alert(" .");
    }
  };

  const handleWithdrawPageButtonClick = () => {
    navigate("/withdraw"); // Withdraw  
  };

  return (
    <div className="form-container">
      <h2 className="form-title"> </h2>
      <form onSubmit={handleSubmit} className="form">
        <div className="form-group">
          <label htmlFor="username" className="form-label">
            :
          </label>
          <input
            type="text"
            id="username"
            className="form-input"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            placeholder=" "
          />
        </div>
        <div className="form-group">
          <label htmlFor="email" className="form-label">
            :
          </label>
          <input
            type="email"
            id="email"
            className="form-input"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            placeholder=" "
          />
        </div>
        <div className="form-group">
          <label htmlFor="phone" className="form-label">
            :
          </label>
          <input
            type="tel"
            id="phone"
            className="form-input"
            value={phone}
            onChange={(e) => setPhone(e.target.value)}
            placeholder=" "
          />
        </div>
        <button type="submit" className="form-button">
          
        </button>
      </form>
      <div>
        <button
          type="button"
          className="withdraw-page-button"
          onClick={handleWithdrawPageButtonClick}
        >
          
        </button>
        <button
          type="button"
          className="logout-button"
          onClick={handleLogoutButtonClick}
        >
          
        </button>
      </div>
    </div>
  );
};

export default MyForm;
