import React, { useState } from "react";
import "./SignupForm.css";
import { useNavigate } from "react-router-dom";
import { doc, setDoc } from "firebase/firestore";
import { createUserWithEmailAndPassword } from "firebase/auth";
import { auth, db } from "../firebase"; // Firebase  

const SignupForm = () => {
  // (state)     
  const [userInfo, setUserInfo] = useState({
    username: "",
    studentId: "",
    emailPrefix: "",
    password: "",
  });

  const [isAgreed, setIsAgreed] = useState(false); //   

  //        
  const handleChange = (e) => {
    const { name, value } = e.target;
    setUserInfo({
      ...userInfo,
      [name]: value,
    });
  };

  //  @   handleChangeEmailPrefix  
  const handleChangeEmailPrefix = (e) => {
    setUserInfo({
      ...userInfo,
      emailPrefix: e.target.value,
    });
  };

  //      
  const handleAgreementChange = (e) => {
    setIsAgreed(e.target.checked);
  };

  //     
  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!isAgreed) {
      alert("  .");
      return;
    }

    try {
      // Firebase Authentication   
      const { user } = await createUserWithEmailAndPassword(
        auth,
        `${userInfo.emailPrefix}@syuin.ac.kr`, //      
        userInfo.password
      );

      // Firestore   
      await setDoc(doc(db, "users", user.uid), {
        username: userInfo.username,
        studentId: userInfo.studentId,
        email: `${userInfo.emailPrefix}@syuin.ac.kr`,
        password: userInfo.password, //    
        createdAt: new Date(),
      });

      //     
      alert(" .");
    } catch (error) {
      console.error("Error signing up:", error);
      alert(" .  .");
    }
  };

  const navigate = useNavigate();
  //    
  const handleSignUpButtonClick = () => {
    navigate("/"); // '/' 
  };


  return (
    <div>
      <form className="AA" onSubmit={handleSubmit}>
        <div>
          <label htmlFor="username"> :</label>
          <input
            className="Sinput-form"
            type="text"
            id="username"
            name="username"
            value={userInfo.username}
            onChange={handleChange}
          />
        </div>
        <div>
          <label htmlFor="studentId">:</label>
          <input
            className="Sinput-form"
            type="text"
            id="studentId"
            name="studentId"
            value={userInfo.studentId}
            onChange={handleChange}
          />
        </div>
        <div>
          <label htmlFor="email">:</label>
          <input
            className="Sinput-form"
            type="text"
            id="emailPrefix"
            name="emailPrefix"
            value={userInfo.emailPrefix}
            onChange={handleChangeEmailPrefix}
          />
          <span>@syuin.ac.kr</span>
        </div>
        <div>
          <label htmlFor="password">:</label>
          <input
            className="Sinput-form"
            type="password"
            id="password"
            name="password"
            value={userInfo.password}
            onChange={handleChange}
          />
        </div>
        <div className="agree">
          <label className="text1">
                     .
            <input
              type="checkbox"
              checked={isAgreed}
              onChange={handleAgreementChange}
            ></input>
          </label>
        </div>
        <div>
          <button
            type="submit"
            disabled={!isAgreed} //     
            style={{
              backgroundColor: isAgreed ? "#4a90e2" : "grey", //      
              color: "white",
              cursor: isAgreed ? "pointer" : "not-allowed", //    
            }}
          >
            
          </button>
        </div>     
        <div>
          <button
            type="button"
            className="button"
            onClick={handleSignUpButtonClick}
          >
            
          </button>
        </div>
      </form>
    </div>
  );
};

export default SignupForm;
